@import url(https://fonts.googleapis.com/css2?family=M+PLUS+1+Code&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Romanesco&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  font-family: 'M PLUS 1 Code', sans-serif;
}

.navbar {
  margin-top: 15px;
  margin-left: 15px;
  margin-right: 15px;
}


.App-logo {
  font-family: 'Romanesco', sans-serif;
  font-size: 55px;
  color:  inherit;
  text-decoration:none;
}

.Own-form {
  margin-left: auto;
  margin-right: auto;
  width: 18em;
}

.Photo-list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  grid-column-gap: 0px;
  grid-row-gap: 4rem;
  margin-top: 4rem;
  margin-bottom: 4rem;
  justify-items: center;
}

.card-img-top {
  min-width: 190px;
  max-width: 200px;
  min-height: 190px;
  max-height: 200px;
}

.Error-label {
  color:darkred;
  margin-top: 10px;
  margin-bottom: 2rem;
}

.card-text {
  text-align: center;
}

.card-body {
  text-align: center;
}

.Message {
  text-align: center;
}

.Like-button, .Photo-delete-button {
	background: none;
	color: inherit;
	border: none;
	font: inherit;
	cursor: pointer;
	outline: inherit;
}

.card {
  position: relative;
}

.card img {
  display: block;
  width: 18rem;
}

.card .Photo-delete-button {
  position: absolute; right:0;
}

